/* eslint-disable */
import react from 'react'
import { View } from '@tarojs/components'
import { Circle, Button } from '@antmjs/vantui'

export default function Demo() {
  const [value, setValue] = react.useState(50)
  return (
    <View>
      <Circle value={value} strokeWidth={6} text="" />
      <Circle
        value={value}
        layerColor="#eeeeee"
        color="#ee0a24"
        text=""
      />
      <Circle
        value={value}
        color={{
          '0%': '#ffd01e',
          '100%': '#ee0a24',
        }}
        text=""
      />
      <Circle value={value} size={120} text="" />

      <Button onClick={() => setValue(value + 10)}></Button>
    </View>
  )
}
